import { createContext, useState, useEffect } from "react";
import { Snackbar, Alert } from "@mui/material";

export const CartContext = createContext();

export function CartProvider({ children }) {
  // Balang Durumu (State Initialization):
  // Uygulama aldnda localStorage kontrol edilir.
  // Eer daha nce kaydedilmi bir sepet verisi varsa geri yklenir (Persistence).
  const [cart, setCart] = useState(() => {
    const savedCart = localStorage.getItem("tarPetsCart");
    return savedCart ? JSON.parse(savedCart) : [];
  });

  // Kullanc Bildirimleri (Snackbar) iin State Ynetimi
  const [openSnackbar, setOpenSnackbar] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");

  // Veri Kalcl (Persistence):
  // Sepet state'i her deitiinde (ekleme/silme/gncelleme),
  // gncel veriyi taraycnn localStorage alanna JSON formatnda kaydederiz.
  useEffect(() => {
    localStorage.setItem("tarPetsCart", JSON.stringify(cart));
  }, [cart]);

  // Bildirim Tetikleyici Yardmc Fonksiyon
  const showNotification = (message) => {
    setSnackbarMessage(message);
    setOpenSnackbar(true);
  };

  const handleCloseSnackbar = (event, reason) => {
    if (reason === 'clickaway') return;
    setOpenSnackbar(false);
  };

  // Sepete Ekleme Fonksiyonu:
  // rn zaten sepetteyse miktarn artrr, yoksa yeni obje olarak listeye ekler.
  const addToCart = (product) => {
    const exist = cart.find((item) => item.id === product.id);
    if (exist) {
      setCart((prev) =>
        prev.map((item) =>
          item.id === product.id ? { ...item, quantity: item.quantity + 1 } : item
        )
      );
    } else {
      setCart((prev) => [...prev, { ...product, quantity: 1 }]);
    }
    // Kullancya ngilizce bildirim gster
    showNotification(`${product.name} added to cart! `);
  };

  // rn Sepetten Kaldrma Fonksiyonu
  const removeFromCart = (id) => {
    setCart((prev) => prev.filter((item) => item.id !== id));
    showNotification("Product removed from cart.");
  };

  // Sepeti Temizleme (Sipari baaryla tamamlandnda veya manuel temizlemede kullanlr)
  const clearCart = () => {
    setCart([]);
  };

  // Miktar Artrma (+)
  const increaseQuantity = (id) => {
    setCart((prev) =>
      prev.map((item) =>
        item.id === id ? { ...item, quantity: item.quantity + 1 } : item
      )
    );
  };

  // Miktar Azaltma (-)
  // Miktar 1'in altna derse ilem yapmaz (negatif deer kontrol).
  const decreaseQuantity = (id) => {
    setCart((prev) =>
      prev.map((item) =>
        item.id === id
          ? { ...item, quantity: item.quantity > 1 ? item.quantity - 1 : 1 }
          : item
      )
    );
  };

  // Toplam Tutar Hesaplama (Derived State):
  // 'reduce' metodu kullanarak dizi iindeki tm rnlerin (fiyat * miktar) toplamn hesaplar.
  const totalPrice = cart.reduce(
    (sum, item) => sum + item.price * item.quantity,
    0
  );

  return (
    <CartContext.Provider
      value={{
        cart,
        addToCart,
        removeFromCart,
        clearCart,
        totalPrice,
        increaseQuantity,
        decreaseQuantity,
      }}
    >
      {children}

      {/* Global Bildirim Bileeni (Snackbar): */}
      {/* Context Provider iinde tanmland iin uygulamann her yerinden (App-wide) eriilebilir. */}
      <Snackbar
        open={openSnackbar}
        autoHideDuration={3000}
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
      >
        <Alert onClose={handleCloseSnackbar} severity="success" variant="filled" sx={{ width: '100%', fontWeight: 'bold' }}>
          {snackbarMessage}
        </Alert>
      </Snackbar>
    </CartContext.Provider>
  );
}