import { useParams, Link } from "react-router-dom";
import { useEffect, useState, useContext } from "react";
import axios from "axios"; 
import { 
  Box, Typography, Button, Container, Grid, Chip, Divider, Alert, Stack, Skeleton 
} from "@mui/material"; 
import { CartContext } from "../context/CartContext";

// konlar
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';
import VerifiedUserIcon from '@mui/icons-material/VerifiedUser';
import RemoveShoppingCartIcon from '@mui/icons-material/RemoveShoppingCart';

const API_BASE_URL = "http://localhost:8080";

function ProductDetails() {
  // Dinamik URL Parametresini Yakalama:
  // URL'deki '/products/:id' ksmndaki ID deerini alr.
  const { id } = useParams();
  
  const { addToCart } = useContext(CartContext);
  const [product, setProduct] = useState(null);
  
  // UI Durumlar (Loading & Error States)
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);

  // Veri ekme ve Yaam Dngs (Lifecycle):
  // ID deitiinde veya sayfa ilk yklendiinde alr.
  
  useEffect(() => {
    // UX yiletirmesi: Yeni bir rn aldnda sayfay en ste kaydr.
    window.scrollTo(0, 0); 
    setLoading(true);
    setError(false);
    
    axios.get(`${API_BASE_URL}/products/${id}`)
      .then((res) => {
        setProduct(res.data);
      })
      .catch(err => {
        console.error("Product Detail Error:", err);
        setError(true);
      })
      .finally(() => setLoading(false));
  }, [id]);

  // Durum 1: Ykleniyor (Skeleton Screen)
  // Kullancya bo beyaz ekran gstermek yerine, ieriin yer tutucularn (Skeleton) gsteriyoruz.
  if (loading) {
    return (
      <Container maxWidth="lg" sx={{ mt: 8, mb: 8 }}>
         <Grid container spacing={5}>
            <Grid item xs={12} md={6}>
               <Skeleton variant="rectangular" height={450} sx={{ borderRadius: "20px" }} animation="wave" />
            </Grid>
            <Grid item xs={12} md={6}>
               <Skeleton width="60%" height={40} sx={{ mb: 2 }} />
               <Skeleton width="40%" height={30} sx={{ mb: 4 }} />
               <Skeleton width="100%" height={100} sx={{ borderRadius: "10px" }} />
            </Grid>
         </Grid>
      </Container>
    );
  }

  // Durum 2: Hata veya rn Bulunamad
  if (error || !product) {
    return (
        <Container sx={{ mt: 10, textAlign: "center" }}>
            <Alert severity="error">Product not found!</Alert>
            <Button component={Link} to="/products" sx={{ mt: 2 }}>Go Back</Button>
        </Container>
    );
  }

  //  Mant (Business Logic):
  // ndirim orann dinamik olarak hesaplama
  const discountRate = product.oldPrice 
    ? Math.round(((product.oldPrice - product.price) / product.oldPrice) * 100) 
    : 0;
  
  // Stok Kontrol
  const isOutOfStock = product.stock === 0;

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 8 }}>
      
      {/* Navigasyon: Geri Dn Butonu */}
      <Button component={Link} to="/products" startIcon={<ArrowBackIcon />} sx={{ mb: 3, color: "gray" }}>
        Back to Products
      </Button>

      <Grid container spacing={6} alignItems="center">
        
        {/* SOL TARAF: RN GRSEL */}
        <Grid item xs={12} md={6}>
          <Box 
            sx={{ 
                height: { xs: "350px", md: "500px" }, 
                width: "100%",
                display: "flex", 
                alignItems: "center", 
                justifyContent: "center",
                borderRadius: "24px", 
                backgroundColor: "#fff", 
                position: "relative", 
                overflow: "hidden",
                boxShadow: "0 10px 40px rgba(0,0,0,0.08)" 
            }}
          >
            {/* ndirim Rozeti (Conditional Rendering) */}
            {product.isDiscounted && (
              <Chip 
                label={`${discountRate}% OFF`} 
                color="error" 
                sx={{ 
                    position: "absolute", top: 20, left: 20, 
                    fontWeight: "bold", fontSize: "0.9rem", py: 0.5, zIndex: 2 
                }} 
              />
            )}

            <img
              src={product.imageUrl}
              alt={product.name}
              style={{ 
                  maxHeight: "320px", 
                  maxWidth: "80%",  
                  objectFit: "contain",
                  display: "block"
              }}
            />
          </Box>
        </Grid>

        {/* SA TARAF: RN BLGLER */}
        <Grid item xs={12} md={6} sx={{ display: "flex", flexDirection: "column", justifyContent: "center" }}>
          
          {/* Kategori Yolu (Breadcrumb benzeri) */}
          <Typography variant="overline" sx={{ color: "gray", fontSize: "13px", letterSpacing: 1.2, fontWeight: "bold" }}>
            {product.category} / {product.subcategory}
          </Typography>

          <Typography variant="h3" sx={{ fontWeight: "800", color: "#2d2d2d", mb: 2, fontSize: { xs: "2rem", md: "2.5rem" } }}>
            {product.name}
          </Typography>

          {/* Fiyat Alan */}
          <Box sx={{ display: "flex", alignItems: "center", gap: 2, mb: 4 }}>
            {product.oldPrice && (
              <Typography sx={{ textDecoration: "line-through", color: "#999", fontSize: "1.2rem" }}>
                {product.oldPrice}
              </Typography>
            )}
            <Typography sx={{ fontSize: "2.5rem", color: "#F59E0B", fontWeight: "900", lineHeight: 1 }}>
              {product.price}
            </Typography>
          </Box>

          {/* Gven Rozetleri */}
          <Stack direction="row" spacing={1} sx={{ mb: 4, flexWrap: "wrap", gap: 1 }}>
            {product.price > 500 && (
              <Chip icon={<LocalShippingIcon />} label="Free Shipping" color="success" variant="outlined" sx={{fontWeight: "bold"}} />
            )}
            <Chip icon={<VerifiedUserIcon />} label="Genuine Product" color="primary" variant="outlined" sx={{fontWeight: "bold"}} />
          </Stack>

          <Divider sx={{ mb: 3 }} />

          {/* Aklama */}
          <Typography variant="h6" sx={{ fontWeight: "bold", mb: 1, fontSize: "1.1rem" }}>Description</Typography>
          <Typography variant="body1" sx={{ color: "#555", lineHeight: 1.6, mb: 4 }}>
            {product.description || "No detailed description available for this product yet."}
          </Typography>

          {/* Aksiyon Butonu (Stok Kontroll) */}
          <Button
            variant="contained"
            size="large"
            disabled={isOutOfStock}
            startIcon={isOutOfStock ? <RemoveShoppingCartIcon /> : <AddShoppingCartIcon />}
            fullWidth
            sx={{
              backgroundColor: "#3C2F2F",
              py: 2,
              fontSize: "1rem",
              fontWeight: "bold",
              borderRadius: "12px",
              boxShadow: "0 8px 24px rgba(60, 47, 47, 0.2)",
              "&:hover": { backgroundColor: "#59423f", transform: "translateY(-2px)" },
              "&.Mui-disabled": { backgroundColor: "#e0e0e0" },
              transition: "0.2s"
            }}
            onClick={() => addToCart(product)}
          >
            {isOutOfStock ? "Out of Stock" : "Add to Cart"}
          </Button>
          
          {!isOutOfStock && product.price > 500 && (
            <Alert severity="success" sx={{ mt: 3, borderRadius: "10px", fontWeight: "500" }}>
              Free shipping available for this item! 
            </Alert>
          )}

        </Grid>
      </Grid>
    </Container>
  );
}

export default ProductDetails;