import React, { useState, useContext } from "react";
import {
  Box, Button, TextField, Typography, Paper, MenuItem,
  Snackbar, Alert, CircularProgress, InputAdornment, Stack
} from "@mui/material";
import { useNavigate, Link } from "react-router-dom";

// konlar
import PetsIcon from '@mui/icons-material/Pets';
import PersonIcon from '@mui/icons-material/Person';
import EmailIcon from '@mui/icons-material/Email';
import LockIcon from '@mui/icons-material/Lock';
import PhoneIcon from '@mui/icons-material/Phone';

// Servis Katman: Kayt ilemini backend'e ileten fonksiyon
import { registerUser, loginUser } from "../service/authService";
import { AuthContext } from '../context/AuthContext';

function Register() {
  const { login } = useContext(AuthContext);
  const navigate = useNavigate();

  // Form State Ynetimi: Kullanc verilerini tutar
  const [form, setForm] = useState({
    name: "",
    email: "",
    password: "",
    phone: "",
    petCount: ""
  });

  // Dinamik Form State'i: Evcil hayvan detaylar (tr ve isim)
  const [petDetails, setPetDetails] = useState([]);

  // UI Durumlar (Ykleniyor, Hata, Baar)
  const [loading, setLoading] = useState(false);
  const [errorOpen, setErrorOpen] = useState(false);
  const [successOpen, setSuccessOpen] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");

  // Genel Input Deiiklii Ynetimi
  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  // Telefon Numaras Formatlama: Sadece rakamlara izin verir
  const handlePhoneChange = (e) => {
    const onlyNums = e.target.value.replace(/\D/g, "");
    if (onlyNums.length <= 11) {
      setForm({ ...form, phone: onlyNums });
    }
  };

  // Dinamik Alan Oluturma Mant:
  // Girilen evcil hayvan saysna gre dinamik olarak input alanlar oluturur.
  const handleCountChange = (e) => {
    const val = e.target.value;
    if (val < 0 || val > 10) return; // Maksimum 10 hayvan snr

    const count = parseInt(val) || 0;
    setForm({ ...form, petCount: val });

    // Mevcut verileri koruyarak yeni dizi olutur (State Preservation)
    const newDetails = Array.from({ length: count }, (_, i) => {
      return petDetails[i] || { type: "", name: "" };
    });
    setPetDetails(newDetails);
  };

  // Dinamik Input Deerlerini Gncelleme
  const handlePetDetailChange = (index, field, value) => {
    const updatedPets = [...petDetails];
    updatedPets[index][field] = value;
    setPetDetails(updatedPets);
  };

  // Validasyon Kurallar
  const isPhoneError = form.phone.length > 0 && (form.phone.length < 11 || !form.phone.startsWith("05"));
  const isFormValid = form.name && form.email && form.password && !isPhoneError && form.phone.length === 11;

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (isPhoneError) return;

    setLoading(true);
    setErrorOpen(false);

    // Veri Dntrme (Data Transformation):
    const collectedTypes = petDetails
      .map(p => p.type)
      .filter(t => t)
      .join(", ");

    const dataToSend = {
      name: form.name,
      email: form.email,
      password: form.password,
      phone: form.phone,
      petCount: Number(form.petCount) || 0,
      petType: collectedTypes || "Not Specified"
    };

    try {
      // Servis ars (Service Call)
      const registerResponse = await registerUser(dataToSend);
      // Eer backend register sonras AuthResponse dnyorsa onu kullanarak login et
      if (registerResponse && registerResponse.token) {
        login(registerResponse);
      } else {
        // Fallback: eer sadece message dnmse, normal login istei at
        const userData = await loginUser({ email: form.email, password: form.password });
        login(userData);
      }

      setSuccessOpen(true);
      // Baarl kayt sonras Login sayfasna ynlendir yerine ana sayfaya gidilsin
      setTimeout(() => navigate("/"), 1500);

    } catch (error) {
      console.error("Registration Error:", error);
      // Backend'den dnen hata mesajn yakala ve kullancya gster
      const message = error.response?.data?.message || "An error occurred during registration.";
      setErrorMessage(message);
      setErrorOpen(true);
    } finally {
      setLoading(false);
    }
  };

  // Seilebilir Hayvan Trleri
  const petTypes = [
    { value: 'cat', label: 'Cat' },
    { value: 'dog', label: 'Dog' },
    { value: 'bird', label: 'Bird' },
    { value: 'fish', label: 'Fish' },
    { value: 'rodent', label: 'Rodent' },
    { value: 'reptile', label: 'Reptile' },
  ];

  return (
    <>
      <Box sx={{ minHeight: "100vh", display: "flex", alignItems: "center", justifyContent: "center", backgroundColor: "#FFF8E1", py: 5 }}>
        <Paper sx={{ p: 4, width: 450, borderRadius: 4 }} elevation={10}>

          <Box sx={{ mb: 2, display: "flex", justifyContent: "center" }}>
            <PetsIcon sx={{ fontSize: 50, color: "#F59E0B" }} />
          </Box>

          <Typography variant="h5" textAlign="center" fontWeight="bold" mb={1} color="#3C2F2F">
            Welcome to the Family 
          </Typography>
          <Typography variant="body2" textAlign="center" color="text.secondary" mb={3}>
            Only the best for you and your little friends!
          </Typography>

          <Box component="form" onSubmit={handleSubmit}>
            <Stack spacing={2}>
              {/* Ad Soyad Alan */}
              <TextField
                label="Full Name"
                name="name"
                required
                fullWidth
                value={form.name}
                onChange={handleChange}
                InputProps={{ startAdornment: <InputAdornment position="start"><PersonIcon color="action" /></InputAdornment> }}
              />

              {/* Email Alan */}
              <TextField
                label="Email"
                name="email"
                type="email"
                required
                fullWidth
                value={form.email}
                onChange={handleChange}
                InputProps={{ startAdornment: <InputAdornment position="start"><EmailIcon color="action" /></InputAdornment> }}
              />

              {/* ifre Alan */}
              <TextField
                label="Password"
                name="password"
                type="password"
                required
                fullWidth
                value={form.password}
                onChange={handleChange}
                InputProps={{ startAdornment: <InputAdornment position="start"><LockIcon color="action" /></InputAdornment> }}
              />

              {/* Telefon Alan */}
              <TextField
                label="Phone Number"
                name="phone"
                required
                fullWidth
                value={form.phone}
                onChange={handlePhoneChange}
                error={isPhoneError}
                helperText={isPhoneError ? "Enter 11 digits starting with '05'." : ""}
                InputProps={{
                  startAdornment: <InputAdornment position="start"><PhoneIcon color="action" /></InputAdornment>,
                  inputProps: { maxLength: 11 }
                }}
                placeholder="05xxxxxxxxx"
              />

              {/* Hayvan Says Alan */}
              <TextField
                label="How many pets do you have?"
                name="petCount"
                type="number"
                fullWidth
                InputProps={{ inputProps: { min: 0, max: 10 } }}
                value={form.petCount}
                onChange={handleCountChange}
                placeholder="e.g. 1"
              />

              {/* DNAMK ALANLAR: Hayvan says kadar dng oluturur */}
              {petDetails.map((pet, index) => (
                <Box key={index} sx={{ p: 2, border: "1px dashed #F59E0B", borderRadius: 2, bgcolor: "#FFF3E0" }}>
                  <Typography variant="subtitle2" sx={{ mb: 1, color: "#D97706", fontWeight: "bold" }}>
                    {index + 1}. Pet Information
                  </Typography>

                  <Stack direction="row" spacing={2}>
                    <TextField
                      select
                      label="Type"
                      fullWidth
                      size="small"
                      value={pet.type}
                      onChange={(e) => handlePetDetailChange(index, "type", e.target.value)}
                      sx={{ bgcolor: "white" }}
                    >
                      {petTypes.map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                          {option.label}
                        </MenuItem>
                      ))}
                    </TextField>

                    <TextField
                      label="Name"
                      fullWidth
                      size="small"
                      value={pet.name}
                      onChange={(e) => handlePetDetailChange(index, "name", e.target.value)}
                      sx={{ bgcolor: "white" }}
                    />
                  </Stack>
                </Box>
              ))}

              <Button
                type="submit"
                fullWidth
                variant="contained"
                disabled={loading || !isFormValid}
                sx={{
                  mt: 2,
                  height: 50,
                  bgcolor: "#F59E0B",
                  fontWeight: "bold",
                  fontSize: "16px",
                  borderRadius: "25px",
                  textTransform: "none",
                  ":hover": { bgcolor: "#D97706" }
                }}
              >
                {loading ? <CircularProgress size={24} color="inherit" /> : "Register"}
              </Button>

              <Box sx={{ textAlign: "center", mt: 2 }}>
                <Typography variant="body2">
                  Already have an account?{" "}
                  <Link to="/login" style={{ color: "#F59E0B", fontWeight: "bold", textDecoration: "none" }}>
                    Login here
                  </Link>
                </Typography>
              </Box>

            </Stack>
          </Box>
        </Paper>
      </Box>

      {/* Hata Bildirimi */}
      <Snackbar open={errorOpen} autoHideDuration={4000} onClose={() => setErrorOpen(false)} anchorOrigin={{ vertical: "bottom", horizontal: "center" }}>
        <Alert severity="error" variant="filled" sx={{ width: "100%" }}>{errorMessage}</Alert>
      </Snackbar>

      {/* Baar Bildirimi */}
      <Snackbar open={successOpen} autoHideDuration={2000} onClose={() => setSuccessOpen(false)} anchorOrigin={{ vertical: "bottom", horizontal: "center" }}>
        <Alert severity="success" variant="filled" sx={{ width: "100%" }}>Registration successful! Redirecting to login...</Alert>
      </Snackbar>
    </>
  );
}

export default Register;
