import React, { useEffect, useState, useContext } from "react";
import { AuthContext } from "../context/AuthContext";
import axios from "axios";
import { useNavigate } from "react-router-dom"; 
import { 
  Container, Typography, Paper, Box, Chip, Divider, 
  Grid, CircularProgress, Button, Alert 
} from "@mui/material";

// konlar
import ShoppingBagIcon from '@mui/icons-material/ShoppingBag';
import EventIcon from '@mui/icons-material/Event';
import CreditCardIcon from '@mui/icons-material/CreditCard';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';

const API_BASE_URL = "http://localhost:8080";

function MyOrders() {
  // Context ve State Tanmlar
  const { user } = useContext(AuthContext);
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null); 
  const navigate = useNavigate();

  // Veri ekme (Data Fetching) lemi
  useEffect(() => {
    if (user) {
      // API Endpoint: Sadece giri yapan kullancnn ID'sine ait siparileri eker
      const url = `${API_BASE_URL}/api/orders/user/${user.id}`;
      
      // Yetkilendirme (Authorization):
      // Backend gvenlii iin localStorage'dan alnan JWT token istee eklenir.
      const token = localStorage.getItem("token");
      const config = {
        headers: {
          Authorization: token ? `Bearer ${token}` : "",
        },
      };

      axios.get(url, config)
        .then((res) => {
          // Sralama Mant: Siparileri tarihe gre yeniden eskiye (descending) sralar.
          // Bylece kullanc en son verdii siparii en stte grr.
          const sortedOrders = res.data.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
          setOrders(sortedOrders);
        })
        .catch((err) => {
          console.error("Orders could not be fetched:", err);
          setError("There was a problem loading your orders.");
        })
        .finally(() => setLoading(false));
    } else {
        setLoading(false);
    }
  }, [user]);

  // Durum Kontrol 1: Oturum Almamsa
  if (!user) {
     return (
        <Container sx={{ mt: 15, textAlign: "center" }}>
            <Typography variant="h6" gutterBottom>Please log in to view your orders.</Typography>
            <Button variant="contained" onClick={() => navigate("/login")}>Login</Button>
        </Container>
     );
  }

  // Durum Kontrol 2: Veriler Ykleniyorsa
  if (loading) {
    return <Box sx={{ display:"flex", justifyContent:"center", mt:10 }}><CircularProgress /></Box>;
  }

  // Durum Kontrol 3: Hata Olumusa
  if (error) {
    return (
        <Container sx={{ mt: 10 }}>
            <Alert severity="error">{error}</Alert>
        </Container>
    );
  }

  // Durum Kontrol 4: Sipari Gemii Bosa
  if (orders.length === 0) {
    return (
        <Container sx={{ textAlign: "center", mt: 10 }}>
            <ShoppingBagIcon sx={{ fontSize: 100, color: "#e0e0e0", mb: 2 }} />
            <Typography variant="h5" sx={{ color: "gray", fontWeight: "bold" }}>No orders yet.</Typography>
            <Typography variant="body2" color="text.secondary">Start shopping for your little friend! </Typography>
            <Button variant="outlined" sx={{ mt: 2 }} onClick={() => navigate("/")}>Start Shopping</Button>
        </Container>
    );
  }

  return (
    <Container maxWidth="md" sx={{ mt: 5, mb: 10 }}>
      {/* Sayfa Bal */}
      <Typography variant="h4" fontWeight="bold" mb={4} sx={{ display:'flex', alignItems:'center', gap:2, color: "#3C2F2F" }}>
        <ShoppingBagIcon fontSize="large" sx={{ color: "#F59E0B" }} /> My Order History
      </Typography>

      {/* Sipari Listesi Dngs */}
      {orders.map((order) => (
        <Paper key={order.id} elevation={3} sx={{ p: 3, mb: 4, borderRadius: 3, borderLeft: "6px solid #F59E0B", position: "relative", overflow: "hidden" }}>
          
          {/* Sipari st Bilgisi (Header) */}
          <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "flex-start", mb: 2, flexWrap: "wrap", gap: 2 }}>
             <Box>
                <Typography variant="h6" fontWeight="bold">Order #{order.id}</Typography>
                
                {/* Tarih Formatlama: Gn/Ay/Yl (en-GB standard) */}
                <Typography variant="body2" color="text.secondary" sx={{ display:'flex', alignItems:'center', gap:0.5, mt: 0.5 }}>
                   <EventIcon fontSize="small" /> 
                   {order.createdAt ? new Date(order.createdAt).toLocaleDateString("en-GB") : "-"} 
                   &nbsp;  &nbsp; 
                   {order.createdAt ? new Date(order.createdAt).toLocaleTimeString("en-GB", {hour: '2-digit', minute:'2-digit'}) : ""}
                </Typography>
             </Box>

             {/* Statik Durum Etiketleri (Mock Status) */}
             <Box sx={{ display: 'flex', gap: 1 }}>
                <Chip 
                    label="Preparing" 
                    icon={<LocalShippingIcon />} 
                    color="warning" 
                    variant="filled"
                    size="small"
                />
                <Chip 
                    label="Payment Received" 
                    icon={<CreditCardIcon />} 
                    color="success" 
                    variant="outlined"
                    size="small"
                    sx={{ fontWeight: "bold" }}
                />
             </Box>
          </Box>

          <Divider sx={{ mb: 2 }} />

          {/* Sipari erii (rnler) */}
          <Grid container spacing={2}>
            {order.items && order.items.map((item, index) => (
               <Grid item xs={12} sm={6} key={index}>
                  <Box sx={{ display: "flex", alignItems: "center", gap: 2, p: 1.5, border: "1px solid #f0f0f0", borderRadius: 2, backgroundColor: "#fafafa" }}>
                     <Box>
                        <Typography variant="subtitle2" fontWeight="bold">
                            {item.product ? item.product.name : "Product Info Unavailable"}
                        </Typography>
                        <Typography variant="body2" color="text.secondary">
                           {item.quantity} qty x {item.price} 
                        </Typography>
                     </Box>
                  </Box>
               </Grid>
            ))}
          </Grid>

          <Divider sx={{ my: 2 }} />

          {/* Toplam Tutar */}
          <Box sx={{ display: "flex", justifyContent: "flex-end", alignItems: "center" }}>
             <Typography variant="body1" sx={{ color: "gray", mr: 1 }}>Total Amount:</Typography>
             <Typography variant="h5" fontWeight="900" sx={{ color: "#F59E0B" }}>
                {order.totalPrice} 
             </Typography>
          </Box>

        </Paper>
      ))}
    </Container>
  );
}

export default MyOrders;