import axios from "axios";

// Backend sunucusunun alt temel adres (ENV zerinden alnr, yoksa localhost:8080 fallback)
const API_BASE_URL = process.env.REACT_APP_API_URL || "http://localhost:8080";

// Global konfigrasyona sahip zelletirilmi Axios rnei
const api = axios.create({
  baseURL: API_BASE_URL,
  headers: {
    "Content-Type": "application/json",
  },
});

// stek Araya Giricisi (Request Interceptor):
// Her HTTP isteinden hemen nce alr. Eer localStorage'da bir token varsa,
// bunu 'Authorization' balna ekleyerek gvenli istek atlmasn salar.
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem("token");
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => Promise.reject(error)
);

export default api;