import React from 'react'; 
import ReactDOM from 'react-dom/client'; // React Sanal DOM'unu tarayc DOM'una balayan ktphane
import './index.css'; // Uygulama genelinde geerli olan global stil tanmlar (Global Styles)
import App from './App'; // Tm Provider'lar ve Router' kapsayan kk bileen (Root Component)
import reportWebVitals from './reportWebVitals'; // Performans lmleme arac

// Uygulamann Balanma Noktas (Mount Point):
// 'public/index.html' dosyasndaki "root" id'li div elementini seer.
// React, tm Single Page Application (SPA) yapsn bu kutunun iine ina eder.
const root = ReactDOM.createRoot(document.getElementById('root')); 

root.render(
  // StrictMode: Sadece gelitirme ortamnda (Development) alr.
  // Uygulamadaki olas hatalar, yan etkileri ve eski (deprecated) kullanmlar
  // tespit etmek iin bileenleri kastl olarak iki kez render eder.
  <React.StrictMode>
    <App /> 
  </React.StrictMode>
);

// stee bal: Web Vitals ile performans metriklerini lmler (FCP, LCP vb.)
reportWebVitals();