import { useEffect, useContext, useRef, useState } from "react";
import { CartContext } from "../context/CartContext";
import { AuthContext } from "../context/AuthContext";
import {
  Container,
  Typography,
  Button,
  Paper,
  Box,
  CircularProgress,
  Stack,
  Alert,
} from "@mui/material";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import LocalMallIcon from "@mui/icons-material/LocalMall";
import HomeIcon from "@mui/icons-material/Home";
import { useNavigate } from "react-router-dom";
import Confetti from "react-confetti";

// Servis Katman
import { createOrder } from "../service/orderService";

const CART_KEY = "tarPetsCart";

function Success() {
  const { cart, clearCart } = useContext(CartContext);
  const { user } = useContext(AuthContext);
  const navigate = useNavigate();

  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Idempotency Kontrol:
  // React.StrictMode gelitirme ortamnda useEffect'i iki kez altrr.
  // Bu ref, sipariin veritabanna yanllkla iki kez kaydedilmesini (Double Posting) engeller.
  const orderCreated = useRef(false);

  // Pencere boyutunu izleme (Konfetilerin tam ekran olmas iin)
  const [windowSize, setWindowSize] = useState({
    width: window.innerWidth,
    height: window.innerHeight,
  });

  useEffect(() => {
    const handleResize = () =>
      setWindowSize({ width: window.innerWidth, height: window.innerHeight });
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  useEffect(() => {
    // Gvenlik ve Mkerrer Kayt Kontrol
    if (!user) return;
    if (orderCreated.current) return;

    // Veri Kurtarma (State Recovery):
    // Stripe gibi 3. parti sitelerden geri dnldnde React State sfrlanabilir.
    // Eer Context bosa, veriyi LocalStorage yedeinden (Persistence Layer) kurtaryoruz.
    let finalCart = Array.isArray(cart) ? cart : [];
    if (finalCart.length === 0) {
      const storedCart = localStorage.getItem(CART_KEY);
      if (storedCart) {
        try {
          finalCart = JSON.parse(storedCart) || [];
        } catch (e) {
          finalCart = [];
        }
      }
    }

    // Eer sepet hala bosa ilem yapma (Hatal ynlendirme korumas)
    if (finalCart.length === 0) {
      setLoading(false);
      return;
    }

    // lem bayran iaretle (Kilit mekanizmas)
    orderCreated.current = true;

    // Backend iin Sipari Verisini Hazrlama (DTO Mapping)
    const calculatedTotalPrice = finalCart.reduce(
      (acc, item) => acc + item.price * item.quantity,
      0
    );

    const orderData = {
      userId: user.id,
      totalPrice: calculatedTotalPrice,
      items: finalCart.map((item) => ({
        productId: item.id,
        price: item.price,
        quantity: item.quantity,
      })),
    };

    // Servis ars: Siparii Veritabanna Kaydet
    createOrder(orderData)
      .then((res) => {
        // lem Baarl: Sepeti ve nbellei temizle
        clearCart();
        localStorage.removeItem(CART_KEY);
      })
      .catch((err) => {
        console.error("Order Service Error:", err);
        setError(
          "Your payment was successful, but there was an error saving your order. Please contact support."
        );
      })
      .finally(() => {
        // Kullanc deneyimi iin ykleme ekrann biraz beklet
        setTimeout(() => setLoading(false), 800);
      });
  }, [user, cart, clearCart]);

  // Durum 1: lem Sryor (Loading)
  if (loading) {
    return (
      <Box sx={{ display: "flex", flexDirection: "column", alignItems: "center", mt: 15 }}>
        <CircularProgress color="success" size={60} />
        <Typography sx={{ mt: 3, fontWeight: "bold", color: "#555" }}>
          Processing your order, please wait...
        </Typography>
      </Box>
    );
  }

  // Durum 2: Backend Hatas (Error)
  if (error) {
    return (
      <Container maxWidth="sm" sx={{ mt: 8 }}>
        <Alert severity="error" variant="filled" sx={{ mb: 2 }}>
          {error}
        </Alert>
        <Button variant="contained" onClick={() => navigate("/")} sx={{ mt: 2 }}>
          Back to Homepage
        </Button>
      </Container>
    );
  }

  // Durum 3: Baarl lem (Success UI)
  return (
    <>
      {/* Kutlama Efekti (Confetti) */}
      <Confetti
        width={windowSize.width}
        height={windowSize.height}
        numberOfPieces={200}
        recycle={false}
      />

      <Container maxWidth="sm" sx={{ mt: 8, mb: 10, position: "relative", zIndex: 1 }}>
        <Paper
          elevation={6}
          sx={{
            p: 5,
            borderRadius: "24px",
            textAlign: "center",
            background: "linear-gradient(135deg, #ffffff 0%, #f0fdf4 100%)",
            border: "1px solid #dcfce7",
          }}
        >
          <CheckCircleIcon
            sx={{
              fontSize: 100,
              color: "#22c55e",
              mb: 2,
              filter: "drop-shadow(0px 4px 10px rgba(34, 197, 94, 0.3))",
            }}
          />

          <Typography variant="h4" fontWeight="900" sx={{ color: "#15803d", mb: 1 }}>
            Payment Successful! 
          </Typography>

          <Typography variant="body1" color="text.secondary" sx={{ mb: 4, fontSize: "1.1rem" }}>
            Your order has been successfully placed and is being prepared.
            <br />
            Our little friend will love the gifts! 
          </Typography>

          {/* Aksiyon Butonlar */}
          <Stack direction={{ xs: "column", sm: "row" }} spacing={2} justifyContent="center">
            <Button
              variant="contained"
              size="large"
              startIcon={<HomeIcon />}
              onClick={() => navigate("/")}
              sx={{
                bgcolor: "#3C2F2F",
                borderRadius: "50px",
                px: 4,
                py: 1.5,
                textTransform: "none",
                fontWeight: "bold",
              }}
            >
              Home
            </Button>

            <Button
              variant="outlined"
              size="large"
              startIcon={<LocalMallIcon />}
              onClick={() => navigate("/orders")}
              sx={{
                color: "#3C2F2F",
                borderColor: "#3C2F2F",
                borderRadius: "50px",
                px: 4,
                py: 1.5,
                textTransform: "none",
                fontWeight: "bold",
                borderWidth: "2px",
                "&:hover": {
                  borderWidth: "2px",
                  bgcolor: "rgba(60, 47, 47, 0.05)",
                },
              }}
            >
              My Orders
            </Button>
          </Stack>
        </Paper>
      </Container>
    </>
  );
}

export default Success;