import { BrowserRouter as Router, Routes, Route } from "react-router-dom"; 
import { ThemeProvider, CssBaseline, Box } from "@mui/material"; 
import theme from "./theme"; 

// Sayfa Bileenleri (Pages)
import Login from "./pages/Login";
import Register from "./pages/Register";
import Checkout from "./pages/Checkout";
import MyOrders from "./pages/MyOrders";
import Success from "./pages/Success";
import AdminPanel from "./pages/AdminPanel";
import Home from "./pages/Home";
import Products from "./pages/Products";
import Cart from "./pages/Cart";
import ProductDetails from "./pages/ProductDetails";
import MyPet from "./pages/MyPet";

// Global State (Context) Salayclar
import { AuthProvider } from "./context/AuthContext";
import { CartProvider } from "./context/CartContext";

// UI Bileenleri
import Navbar from "./components/Navbar";
import Footer from "./components/Footer";

function App() {
  return (
    // Tema Salayc: Tm uygulama genelinde MUI tema ayarlarn (renk, font) uygular.
    <ThemeProvider theme={theme}>
      <CssBaseline /> {/* Tarayc varsaylan stillerini sfrlar (Normalize CSS) */}
      
      {/* Provider Pattern (Sarmalayclar): */}
      {/* Auth ve Cart verilerine uygulamann her yerinden eriilebilmesi iin
          Router' bu context salayclar ile sarmalyoruz. */}
      <AuthProvider>
        <CartProvider>
          
          {/* stemci Taraf Ynlendirme (Client-Side Routing) */}
          <Router>
            
            {/* Ana Dzen (Layout) Yaps: */}
            {/* Flexbox kullanarak 'Sticky Footer' yapsn kuruyoruz. 
                erik az olsa bile Footer her zaman sayfann en altnda kalr. */}
            <Box 
              sx={{ 
                display: "flex", 
                flexDirection: "column", 
                minHeight: "100vh", 
                backgroundColor: "#fafafa"
              }}>
              
              <Navbar />
              
              {/* Dinamik erik Alan: */}
              {/* flexGrow: 1 zellii ile bo alan kaplayarak footer' aa iter. */}
              <Box sx={{ flexGrow: 1 }}>
                <Routes>
                  {/* --- GENEL SAYFALAR --- */}
                  <Route path="/" element={<Home />} />
                  
                  {/* --- KMLK DORULAMA (AUTH) --- */}
                  <Route path="/login" element={<Login />} />
                  <Route path="/register" element={<Register />} />

                  {/* --- ALIVER VE RNLER --- */}
                  <Route path="/products" element={<Products />} />
                  <Route path="/products/:id" element={<ProductDetails />} /> {/* Dinamik Route (URL Parametresi) */}
                  <Route path="/cart" element={<Cart />} />

                  {/* --- DEME SREC --- */}
                  <Route path="/checkout" element={<Checkout />} />
                  <Route path="/success" element={<Success />} />
                  <Route path="/checkout/success" element={<Success />} /> {/* Yedek ynlendirme yolu */}

                  {/* --- KULLANICI VE YNETC --- */}
                  <Route path="/orders" element={<MyOrders />} />
                  <Route path="/mypet" element={<MyPet />} /> 
                  <Route path="/admin" element={<AdminPanel />} /> {/* RBAC ile korunur */}
                  
                  {/* --- 404 / HATA YNETM --- */}
                  {/* Tanml olmayan bir yola gidilirse Ana Sayfaya ynlendir (Wildcard) */}
                  <Route path="*" element={<Home />} />
                </Routes>
              </Box>
              
              <Footer />
            </Box>
          </Router>
        </CartProvider>
      </AuthProvider>
    </ThemeProvider>
  );
}

export default App;