import api from "../api/axios";

// Stripe deme oturumunu balatan servis
export const createCheckoutSession = async (checkoutData) => {
  const response = await api.post("/api/create-checkout-session", checkoutData);
  return response.data;
};

// Veritabanna yeni sipari kayd oluturan servis
export const createOrder = async (orderData) => {
  const response = await api.post("/api/orders", orderData);
  return response.data;
};

// Kullanc rolne gre sipari listesini getiren servis
// Admin ise tm siparileri, deilse sadece kullancnn kendi siparilerini eker
export const getOrders = async (user) => {
  let url = `/api/orders/user/${user.id}`;
  
  if (user.role === "ADMIN") {
    url = "/api/orders";
  }

  const response = await api.get(url);
  return response.data;
};