import React, { useContext, useEffect, useState } from "react";
import axios from "axios";
import {
  Container, Typography, Table, TableBody, TableCell, TableHead, TableRow,
  Paper, CircularProgress, Alert, Box, Divider, Chip, Button
} from "@mui/material";
import { AuthContext } from "../context/AuthContext";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import { useNavigate } from "react-router-dom";

const API_BASE_URL = "http://localhost:8080";

function AdminPanel() {
  const { user, logout } = useContext(AuthContext);
  const [users, setUsers] = useState([]);
  const [orders, setOrders] = useState([]);

  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const navigate = useNavigate();

  // Gvenlik Kontrol (Client-Side Route Protection):
  // Eer kullanc giri yapmamsa veya rol 'ADMIN' deilse ana sayfaya ynlendirilir.
  // Bu, yetkisiz kullanclarn admin paneline erimesini engeller.
  useEffect(() => {
    if (!user) return;

    if (user.role === "ADMIN") {
      fetchData();
    } else {
      navigate("/"); // Yetkisiz giri denemesi -> Ana sayfaya at
    }
  }, [user, navigate]);

  const fetchData = async () => {
    setLoading(true);
    setError(null);

    try {
      const token = localStorage.getItem("token");
      const config = {
        headers: {
          Authorization: token ? `Bearer ${token}` : "",
        },
      };

      const [usersResponse, ordersResponse] = await Promise.all([
        axios.get(`${API_BASE_URL}/api/admin/users`, config),
        axios.get(`${API_BASE_URL}/api/orders`, config)
      ]);

      setUsers(usersResponse.data);
      setOrders(ordersResponse.data.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt)));

    } catch (err) {
      console.error("Data fetching error:", err);
      if (err.response && err.response.status === 403) {
        // Token might be missing/expired on server (in-memory token store lost after restart)
        setError("Access Denied: Your session token is invalid or expired. Please logout and login again.");
      } else {
        setError("An error occurred while fetching data from the server.");
      }
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <Box sx={{ display: "flex", justifyContent: "center", mt: 15 }}>
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Container maxWidth="xl" sx={{ mt: 6, mb: 10 }}>
      {/* Balk Alan */}
      <Box sx={{ display: "flex", alignItems: "center", gap: 2, mb: 4 }}>
        <AdminPanelSettingsIcon sx={{ fontSize: 40, color: "#d32f2f" }} />
        <Typography variant="h4" fontWeight="bold" color="#3C2F2F">
          Admin Management Panel
        </Typography>
      </Box>

      {error && (
        <Box sx={{ mb: 2 }}>
          <Alert severity="error" sx={{ mb: 2 }}>
            {error}
          </Alert>
          {error.includes("logout") && (
            <Button variant="contained" color="error" onClick={() => { logout(); navigate('/login'); }}>
              Logout and Login
            </Button>
          )}
        </Box>
      )}

      <>
        {/* 1. BLM: KULLANICI LSTES TABLOSU */}
        <Paper sx={{ mt: 3, p: 3, overflowX: "auto", borderRadius: 3 }} elevation={3}>
          <Typography variant="h6" sx={{ mb: 2, color: "#1976d2", fontWeight: "bold", display: 'flex', alignItems: 'center', gap: 1 }}>
             Registered Users ({users.length})
          </Typography>
          <Table>
            <TableHead sx={{ backgroundColor: "#f5f5f5" }}>
              <TableRow>
                <TableCell><b>ID</b></TableCell>
                <TableCell><b>Full Name</b></TableCell>
                <TableCell><b>Email</b></TableCell>
                <TableCell><b>Phone</b></TableCell>
                <TableCell><b>Pet Info</b></TableCell>
                <TableCell><b>Role</b></TableCell>
              </TableRow>
            </TableHead>

            <TableBody>
              {users.map((u) => (
                <TableRow key={u.id} hover>
                  <TableCell>{u.id}</TableCell>
                  <TableCell sx={{ fontWeight: 500 }}>{u.name || "-"}</TableCell>
                  <TableCell>{u.email}</TableCell>
                  <TableCell>{u.phone || "-"}</TableCell>
                  <TableCell>
                    {u.petType ? `${u.petType} (${u.petCount})` : "-"}
                  </TableCell>
                  <TableCell>
                    <Chip
                      label={u.role}
                      color={u.role === "ADMIN" ? "error" : "success"}
                      size="small"
                      variant={u.role === "ADMIN" ? "filled" : "outlined"}
                    />
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </Paper>

        <Divider sx={{ my: 5 }} />

        {/* 2. BLM: SPAR GEM TABLOSU */}
        <Paper sx={{ mt: 3, p: 3, overflowX: "auto", borderRadius: 3, borderTop: "4px solid #F59E0B" }} elevation={3}>
          <Typography variant="h6" sx={{ mb: 2, color: "#F59E0B", fontWeight: "bold", display: 'flex', alignItems: 'center', gap: 1 }}>
             Global Order History ({orders.length})
          </Typography>
          <Table>
            <TableHead sx={{ backgroundColor: "#fff8e1" }}>
              <TableRow>
                <TableCell><b>Order No</b></TableCell>
                <TableCell><b>Customer</b></TableCell>
                <TableCell><b>Products</b></TableCell>
                <TableCell><b>Date</b></TableCell>
                <TableCell align="right"><b>Total Amount</b></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {orders.map((order) => (
                <TableRow key={order.id} hover>
                  <TableCell><b>#{order.id}</b></TableCell>

                  <TableCell>
                    <Box sx={{ display: "flex", flexDirection: "column" }}>
                      {/* Optional chaining (?.) ile null check yapyoruz, hata almay engeller */}
                      <Typography variant="subtitle2" fontWeight="bold">
                        {order.user?.name || "Guest / Deleted"}
                      </Typography>
                      <Typography variant="caption" color="text.secondary">
                        {order.user?.email || "-"}
                      </Typography>
                    </Box>
                  </TableCell>

                  <TableCell>
                    {order.items && order.items.map((item, index) => (
                      <div key={index}>
                         {item.product ? item.product.name : "Product Info Unavailable"}
                        <span style={{ color: 'gray', fontSize: '0.85em', marginLeft: '5px' }}>
                          (x{item.quantity})
                        </span>
                      </div>
                    ))}
                  </TableCell>

                  <TableCell>
                    {/* Tarihi ngiltere formatnda (Gn/Ay/Yl) gster */}
                    {new Date(order.createdAt).toLocaleDateString("en-GB")}
                    <br />
                    <span style={{ fontSize: '0.8em', color: 'gray' }}>
                      {new Date(order.createdAt).toLocaleTimeString("en-GB", { hour: '2-digit', minute: '2-digit' })}
                    </span>
                  </TableCell>

                  <TableCell align="right">
                    <Typography fontWeight="bold" color="success.main">
                      {order.totalPrice} 
                    </Typography>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </Paper>
      </>
    </Container>
  );
}

export default AdminPanel;

