import React, { useEffect, useState, useContext } from "react";
// Servis Katman: Veri ekme ilemini UI'dan ayryoruz (Separation of Concerns).
import { getAllProducts } from "../service/productService"; 

import { 
  Container, 
  Typography, 
  Box, 
  CircularProgress, 
  Alert, 
  Button, 
  Stack, 
  Chip, 
  Fade 
} from "@mui/material";
import { useLocation } from "react-router-dom";
import { CartContext } from "../context/CartContext";

// konlar
import FilterListIcon from '@mui/icons-material/FilterList';
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline';
import RocketLaunchIcon from '@mui/icons-material/RocketLaunch';

// Bileenler
import ProductCard from "../components/ProductCard";

function Products() {
  // State Ynetimi
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  const [selectedSubcat, setSelectedSubcat] = useState("All");

  const { addToCart } = useContext(CartContext);
  
  // URL Parametre Ynetimi (Query Strings):
  // URL'deki '?category=cat' veya '?search=food' gibi parametreleri okur.
  // Bu sayede link paylam yapldnda filtreler korunur (Deep Linking).
  const location = useLocation();
  const searchParams = new URLSearchParams(location.search);
  const categoryParam = searchParams.get("category");
  const searchParam = searchParams.get("search");

  // Veri ekme lemi (Lifecycle):
  // Sayfa yklendiinde tm rnleri servisten eker.
  useEffect(() => {
    setLoading(true);
    
    getAllProducts()
      .then((data) => {
        setProducts(data);
        setError(false);
      })
      .catch((err) => {
        console.error("Products API error:", err);
        setError(true);
      })
      .finally(() => setLoading(false));
  }, []); 

  // UX yiletirmesi:
  // Ana kategori veya arama terimi deitiinde alt kategori filtresini sfrlar.
  useEffect(() => {
    setSelectedSubcat("All");
  }, [categoryParam, searchParam]);

  // Filtreleme Mant (Client-Side Filtering):
  // Performans iin veriyi bir kere ekip taraycda filtreliyoruz.
  const filteredProducts = products.filter((product) => {
    // 1. Arama Filtresi (Case-insensitive)
    if (searchParam) {
      return product.name.toLowerCase().includes(searchParam.toLowerCase());
    }
    // 2. Ana Kategori Filtresi
    if (categoryParam && product.category !== categoryParam) {
      return false;
    }
    // 3. Alt Kategori Filtresi
    if (selectedSubcat !== "All" && product.subcategory !== selectedSubcat) {
      return false;
    }
    return true;
  });

  // Dinamik Alt Kategori karm:
  // Seili ana kategoriye ait rnlerden benzersiz (unique) alt kategorileri hesaplar.
  // Set veri yaps duplicate (tekrar eden) deerleri engeller.
  const currentCategoryProducts = categoryParam 
    ? products.filter(p => p.category === categoryParam) 
    : products;
    
  const subcategories = ["All", ...new Set(currentCategoryProducts.map(p => p.subcategory).filter(Boolean))];

  // Kategori Balklar (Mapping Object)
  const categoryNames = { 
    cat: "Cat Products ", 
    dog: "Dog Products ", 
    bird: "Bird Products ", 
    fish: "Fish Products ",
    rodent: "Rodent Products ", 
    reptile: "Reptile Products ",
  };

  return (
    <Box sx={{ backgroundColor: "#FAFAFA", minHeight: "100vh", py: 5 }}>
      <Container maxWidth="xl">
        
        {/* Balk ve Sonu Bilgisi */}
        <Box sx={{ mb: 4 }}>
          <Typography variant="h4" sx={{ fontWeight: "900", color: "#3C2F2F", display: "flex", alignItems: "center", gap: 1 }}>
            {searchParam 
              ? `Results for "${searchParam}"` 
              : (categoryNames[categoryParam] || "All Products")
            }
            {!searchParam && !categoryParam && <RocketLaunchIcon sx={{ color: "#F59E0B" }} />}
          </Typography>
          
          <Typography variant="body1" sx={{ opacity: 0.7, mt: 1 }}>
            {filteredProducts.length} products found
          </Typography>
        </Box>

        {/* Dinamik Alt Kategori Filtreleri (Chips) */}
        {!loading && subcategories.length > 1 && !searchParam && (
          <Stack direction="row" spacing={1} sx={{ mb: 4, overflowX: "auto", py: 1 }}>
            <FilterListIcon sx={{ color: "gray", alignSelf: "center", mr: 1 }} />
            {subcategories.map((subcat) => (
              <Chip 
                key={subcat} 
                label={subcat} 
                clickable 
                onClick={() => setSelectedSubcat(subcat)} 
                sx={{ 
                  fontWeight: "bold",
                  backgroundColor: selectedSubcat === subcat ? "#F59E0B" : "white",
                  color: selectedSubcat === subcat ? "white" : "gray",
                  border: selectedSubcat === subcat ? "none" : "1px solid #e0e0e0",
                  "&:hover": {
                    backgroundColor: selectedSubcat === subcat ? "#d98c0a" : "#f5f5f5"
                  }
                }} 
              />
            ))}
          </Stack>
        )}

        {/* Durum Ynetimi ve Listeleme */}
        {loading ? (
          <Box sx={{ display: "flex", justifyContent: "center", py: 10 }}>
            <CircularProgress color="warning" size={60} />
          </Box>
        ) : error ? (
          <Alert severity="error" icon={<ErrorOutlineIcon fontSize="inherit" />}>
            Failed to load products! Please check your connection.
          </Alert>
        ) : filteredProducts.length === 0 ? (
          <Box sx={{ textAlign: "center", py: 10 }}>
            <Typography variant="h5" color="text.secondary">No products found. </Typography>
            <Button variant="outlined" sx={{ mt: 2 }} onClick={() => window.location.href = "/products"}>
              Clear Filters
            </Button>
          </Box>
        ) : (
          // Responsive Grid Yaps
          <Box sx={{
            display: "grid",
            gridTemplateColumns: {
              xs: "repeat(1, 1fr)",    
              sm: "repeat(2, 1fr)",    
              md: "repeat(3, 1fr)",    
              lg: "repeat(4, 1fr)",    
              xl: "repeat(4, 1fr)"     
            },
            gap: 3, 
            alignItems: "stretch"
          }}>
            {filteredProducts.map((product) => (
              <Fade in={true} timeout={500} key={product.id}>
                <Box sx={{ height: '100%' }}> 
                  {/* Reusable UI Component kullanm */}
                  <ProductCard product={product} addToCart={addToCart} />
                </Box>
              </Fade>
            ))}
          </Box>
        )}

      </Container>
    </Box>
  );
}

export default Products;