import React, { useContext, useEffect, useState } from "react";
import axios from "axios";
import {
  Box,
  Typography,
  Paper,
  Divider,
  Chip,
  Container,
  CircularProgress,
  Fade 
} from "@mui/material";
import PetsIcon from "@mui/icons-material/Pets";
import { AuthContext } from "../context/AuthContext";
import { CartContext } from "../context/CartContext";
import ProductCard from "../components/ProductCard";

const API_BASE_URL = "http://localhost:8080";

function MyPet() {
  const { user } = useContext(AuthContext);
  const { addToCart } = useContext(CartContext);
  const [recommendedProducts, setRecommendedProducts] = useState([]);
  const [loading, setLoading] = useState(false);
  // Tip bazl saya: { Cat: 2, Dog: 1 }
  const [petTypeCounts, setPetTypeCounts] = useState({});

  // Yardmc: dizi kartrma
  const shuffle = (arr) => {
    const a = arr.slice();
    for (let i = a.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [a[i], a[j]] = [a[j], a[i]];
    }
    return a;
  };

  // Kiiselletirilmi neri Motoru (Personalization Engine):
  // Artk backend'ten ekilen tm pet trlerini dikkate alyoruz.
  useEffect(() => {
    if (!user || !user.id) return;

    let isMounted = true;
    setLoading(true);

    // 1) Kullancnn tam verisini ek (pets array'i iin)
    axios.get(`${API_BASE_URL}/api/users/${user.id}`)
      .then(res => {
        if (!isMounted) return;
        const fullUser = res.data;

        // Hesapla: ayn trlerden ka tane var
        const counts = {};
        if (fullUser.pets && Array.isArray(fullUser.pets) && fullUser.pets.length > 0) {
          fullUser.pets.forEach(p => {
            const t = (p.type || "Unknown").trim();
            if (!t) return;
            counts[t] = (counts[t] || 0) + 1;
          });
        } else {
          // Fallback: eer backend pets gndermiyorsa (eski login response),
          // user.petType ve user.petCount kullan
          if (user.petType) {
            counts[user.petType] = user.petCount || 1;
          }
        }

        setPetTypeCounts(counts);

        // 2) rn nerileri: tm pet trlerine uyan rnleri al
        const petTypes = Object.keys(counts);
        if (petTypes.length === 0) {
          setRecommendedProducts([]);
          return;
        }

        return axios.get(`${API_BASE_URL}/products`)
          .then(prodRes => {
            const prods = prodRes.data || [];

            // Tr bana uygun rnleri grupla (case-insensitive eleme)
            const productsByType = {};
            petTypes.forEach(t => { productsByType[t] = []; });

            prods.forEach(p => {
              if (!p.category) return;
              petTypes.forEach(t => {
                const cat = p.category.toLowerCase();
                const tt = t.toLowerCase();
                if (cat === tt || cat.includes(tt) || tt.includes(cat)) {
                  productsByType[t].push(p);
                }
              });
            });

            // 4 rn pet trleri arasnda dengeli + rastgele dat
            const TOTAL_SLOTS = 4;
            const n = petTypes.length;
            const base = Math.floor(TOTAL_SLOTS / n);
            let remainder = TOTAL_SLOTS - base * n;

            // Balang dalm: her tr base
            const alloc = {};
            petTypes.forEach(t => alloc[t] = base);

            // Randomly distribute remainder slots among types
            const shuffledTypes = shuffle(petTypes);
            let idx = 0;
            while (remainder > 0) {
              alloc[shuffledTypes[idx % shuffledTypes.length]]++;
              idx++;
              remainder--;
            }

            // Seimler: her trden rastgele 'alloc[t]' tane se (tekrarsz)
            const selected = [];
            const usedIds = new Set();

            petTypes.forEach(t => {
              const pool = shuffle(productsByType[t] || []);
              const want = alloc[t] || 0;
              for (let i = 0; i < want && i < pool.length; i++) {
                if (!usedIds.has(pool[i].id)) {
                  selected.push(pool[i]);
                  usedIds.add(pool[i].id);
                }
              }
            });

            // Eer toplam seilen < TOTAL_SLOTS ise, kalanlar tm rnlerden tamamla
            if (selected.length < TOTAL_SLOTS) {
              // Tm uygun rnleri tek bir pool'a al (zgn ve pet type'lara uyan)
              const allFiltered = prods.filter(p => {
                return petTypes.some(t => {
                  if (!p.category) return false;
                  const cat = p.category.toLowerCase();
                  const tt = t.toLowerCase();
                  return cat === tt || cat.includes(tt) || tt.includes(cat);
                });
              });

              const remainingPool = shuffle(allFiltered.filter(p => !usedIds.has(p.id)));
              let ri = 0;
              while (selected.length < TOTAL_SLOTS && ri < remainingPool.length) {
                selected.push(remainingPool[ri]);
                usedIds.add(remainingPool[ri].id);
                ri++;
              }
            }

            // Sonu olarak en fazla TOTAL_SLOTS olacak ekilde set et
            setRecommendedProducts(selected.slice(0, TOTAL_SLOTS));
          })
          .catch(err => {
            console.error("Recommendation products error:", err);
            setRecommendedProducts([]);
          });
      })
      .catch(err => {
        console.error("Fetch user details error:", err);
        // fallback: hesabmzda sadece nceden saklanan user verisi varsa, kullan
        const counts = {};
        if (user && user.petType) counts[user.petType] = user.petCount || 1;
        setPetTypeCounts(counts);
      })
      .finally(() => setLoading(false));

    return () => { isMounted = false; };
  }, [user]);

  // Oturum kontrol
  if (!user) {
    return (
      <Container sx={{ mt: 6, textAlign: "center" }}><Typography>Please log in to view your profile.</Typography></Container>
    );
  }

  return (
    <Box sx={{ minHeight: "100vh", backgroundColor: "#FFF7ED", py: 5 }}>
      
      {/* 1. BLM: KULLANICI PROFL KARTI */}
      <Container maxWidth="sm">
        <Paper sx={{ p: 4, borderRadius: 4, border: "1px solid #ffcc80" }} elevation={4}>
          {/* Avatar ve sim */}
          <Box sx={{ textAlign: "center", mb: 3 }}>
            <Box sx={{ width: 80, height: 80, borderRadius: "50%", backgroundColor: "#F59E0B", display: "flex", alignItems: "center", justifyContent: "center", margin: "0 auto", mb: 2 }}>
                <PetsIcon sx={{ fontSize: 40, color: "white" }} />
            </Box>
            <Typography variant="h5" fontWeight="bold" color="#3C2F2F">{user.name}</Typography>
            <Typography variant="body2" color="text.secondary">{user.email}</Typography>
          </Box>
          
          <Divider sx={{ mb: 2 }}><Chip label="Account Information" size="small" /></Divider>
          
          <Box sx={{ display: "flex", justifyContent: "space-between", mb: 1 }}>
             <Typography fontWeight="bold">Phone:</Typography><Typography>{user.phone || "-"}</Typography>
          </Box>
          <Box sx={{ display: "flex", justifyContent: "space-between", mb: 1 }}>
             <Typography fontWeight="bold">Role:</Typography>
             <Chip label={user.role} color={user.role === "ADMIN" ? "error" : "primary"} size="small" variant="outlined" />
          </Box>
          
          <Divider sx={{ my: 3 }}><Chip label="Pet Information" color="warning" size="small" /></Divider>
          
          {/* Eski tek TYPE/COUNT grnmn kaldrp, tm trleri ve adetlerini gsteriyoruz */}
          <Box sx={{ display: "flex", flexDirection: "column", gap: 1 }}>
            {loading && <CircularProgress size={24} />}

            {Object.keys(petTypeCounts).length === 0 && !loading && (
              <Typography color="text.secondary">No pets specified.</Typography>
            )}

            {Object.entries(petTypeCounts).map(([type, count]) => (
              <Box key={type} sx={{ display: "flex", justifyContent: "space-between", alignItems: "center", px: 1 }}>
                <Box>
                  <Typography variant="caption" color="text.secondary">TYPE</Typography>
                  <Typography variant="h6" fontWeight="bold">{type.toUpperCase()}</Typography>
                </Box>
                <Box>
                  <Typography variant="caption" color="text.secondary">COUNT</Typography>
                  <Typography variant="h6" fontWeight="bold">{count}</Typography>
                </Box>
              </Box>
            ))}

          </Box>
        </Paper>
      </Container>

      {/* 2. BLM: KYE ZEL RN NERLER */}
      {recommendedProducts.length > 0 && (
        <Container maxWidth="xl" sx={{ mt: 8, px: { xs: 2, md: 6 } }}>
          <Typography variant="h5" fontWeight="bold" mb={3} sx={{ color: "#3C2F2F" }}>
             {user.name}, we picked these for your friend:
          </Typography>

          {loading ? <CircularProgress /> : (
            // Responsive Grid Yaps
            <Box sx={{
              display: "grid",
              gridTemplateColumns: {
                xs: "repeat(1, 1fr)",    
                sm: "repeat(2, 1fr)",    
                md: "repeat(3, 1fr)",    
                lg: "repeat(4, 1fr)",    
                xl: "repeat(4, 1fr)"     
              },
              gap: 3, 
              alignItems: "stretch" 
            }}>
              {recommendedProducts.map((product) => (
                <Fade in={true} timeout={500} key={product.id}>
                    {/* Kart yksekliini eitlemek iin wrapper */}
                    <Box sx={{ height: '100%' }}> 
                        <ProductCard product={product} addToCart={addToCart} />
                    </Box>
                </Fade>
              ))}
            </Box>
          )}
        </Container>
      )}
    </Box>
  );
}

export default MyPet;
